import { mapState } from 'vuex'
import { deleteArticle } from '@/api/article'
export default {
  name: 'ArticleMeta',
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    editArticle() {
      this.$router.push({
        name: 'editor',
        params: {
          article: this.article
        }
      })
    },
    async deleteArticle() {
      const slug = this.$route.params.slug
      await deleteArticle(slug)
      this.$router.push('/')
    }
  }
}
